h1 {
  font-size: 32px;
  font-weight: 700;
}

h2 {
  font-size: 24px;
  font-weight: 700;
}

h3 {
  font-size: 20px;
  font-weight: 700;
}

p {
  font-size: 16px;
  line-height: 1.5;
}

input,
textarea,
select {
  padding: 10px;
  border: 1px solid #007bff;
  color: #007bff;
  background-color: #6c757d;
  transition: border-color 0.3s ease-in-out;
}

input:hover,
textarea:hover,
select:hover {
  border-color: #6c757d;
}

.flex {
  display: flex;
  flex-wrap: wrap;
}
.flex.center {
  justify-content: center;
  align-self: center;
  align-items: center;
}

.card {
  background-color: #fff;
  border: 1px solid #343a40;
}

.ant-input-group .ant-input,
.ant-input-group input[type=text],
.ant-input-group input[type=password],
.ant-input-group input[type=date],
.ant-input-group input[type=datetime],
.ant-input-group input[type=datetime-local],
.ant-input-group input[type=email],
.ant-input-group input[type=month],
.ant-input-group input[type=number],
.ant-input-group input[type=search],
.ant-input-group input[type=tel],
.ant-input-group input[type=time],
.ant-input-group input[type=url],
.ant-input-group input[type=week] {
  min-block-size: unset;
}

/* stylelint-disable declaration-no-important */
.welcome-panel-content {
  min-block-size: unset !important;
}

.notice {
  margin-inline-start: 0 !important;
}

